<template>
	<picker mode="multiSelector" range-key="label" :range="range" :value="columnValue" @change="onChange" @columnchange="onColumnchange">
		<slot/>
	</picker>
</template>

<script>
	import areaList from './area.js'
	export default {
		name: 'zhilin-area',
		props: {
			option: {
				type: Array,
				required: true
			},
			value: {
				type: Array,
				required: true
			},
			col: { // 显示列数
				type: Number | Array,
				default: 1
			},
			firstItems: { // [{label: '全国', value: 'zzz'}, {label: '全省', value: 'xxx'}]
				type: Array,
				default: ()=> ([])
			}
		},
		data() {
			return {
				range: [],
				columnValue: []
			}
		},
		watch: {
			option(n) {
				// 处理firstItems选项
				const firstItems = this.firstItems
				if(firstItems.length) {
					;(function addFirstItem(list) {
						if(!list?.length) return
						let lev = list[0].level
						const first = firstItems[lev]
						if(!first) return
						list.unshift(first)
						list.forEach((v, i)=> {
							addFirstItem(v.children)
						})
					})(this.option)
				}
				// 组装range
				const range = []
				let level = 0
				const buildRange = list => {
					if(!list || level >= this.col) return
					if(this.value?.length && level >= this.value.length) return
					range[level] = list
					buildRange(range[level++][0].children)
				}
				buildRange(this.option)
				this.range = range
				
				this.init()
			},
			value(n) {
				this.init()
			}
		},
		methods: {
			init() {
				if(this.value.length && this.range.length) {
					let columnValue = []
					this.value.forEach((v, i)=> {
						this.range[i]?.forEach((val, idx) => {
							if(val.value == v) {
								columnValue[i] = idx
								if(i != this.value.length - 1) {
									this.$set(this.range, i+1, val.children)
								}
							}
						})
					})
					this.columnValue = columnValue
				}
			},
			onChange({ detail: { value } }) {
				let val = value.map((v, i) => this.range[i]?.[v]?.value)
				// #ifdef H5
					for (let i = 0; i < this.col; i++) {
						if(val[i] === undefined && this.range[i]?.length) val[i] = this.range[i][0].value
					}
				// #endif
				// #ifndef H5
					val = val.filter(v=> v != undefined)
				// #endif
				
				this.$emit('input', val)
			},
			onColumnchange({ detail: { column, value } }) {
				// #ifdef H5
				this.columnValue[column] = value
				// #endif
				// #ifndef H5
				this.$set(this.columnValue, column, value)
				// #endif
				
				const list = this.range[column]?.[value].children
				const buildRange = list => {
					if(column >= this.col - 1) return
					const nextColumn = column + 1
					// #ifdef H5
						this.range[nextColumn] = list
					// #endif
					// #ifndef H5
						this.$set(this.range, nextColumn, [])
						this.$set(this.columnValue, nextColumn, 0)
						this.$nextTick(()=>{
							if(this.firstItems[column] && value === 0) {
								this.range.forEach((v, i)=> {
									if(i > column) this.$set(this.range, i, [])
								})
							}else{
								this.$set(this.range, nextColumn, list || [])
							}
						})
					// #endif
					const children = list?.[this.columnValue[nextColumn]]?.children
					column++
					buildRange(children)
				}
				buildRange(list)
			}
		}
	}
</script>